<script>
  import { onMount, createEventDispatcher } from "svelte"
  import { formatDate, getDefaultHeaders } from "../utils.js"
  const dispatch = new createEventDispatcher()

  export let anouncement
  let title
  let description

  onMount(async () => {
    title = anouncement.title
    description = anouncement.description
  })

  async function updateAnouncements() {
    await fetch("/api/v1/anouncement", {
      headers: getDefaultHeaders(),
      method: "PUT",
      body: JSON.stringify({
        id: anouncement.id,
        title,
        description,
      }),
    }).then(async () => await dispatch("refreshAnouncements"))
  }
</script>

Date created: {formatDate(anouncement.dateCreated)}

<div class="mb-3 row">
  <label
    >Title: <input class="form-control" type="text" bind:value={title} /></label
  >
</div>

<div class="mb-3 row">
  <label
    >Description: <input
      class="form-control"
      type="text"
      bind:value={description}
    /></label
  >
</div>
<div class="modal-footer ">
  <button
    class="btn btn-warning"
    on:click={async () => await updateAnouncements()}>Submit</button
  >
</div>
