<script>
  import { createEventDispatcher } from "svelte"
  import AddQuiz from "./Quiz/AddQuiz.svelte"
  import EditQuiz from "./Quiz/EditQuiz.svelte"
  import QuizTable from "./Quiz/QuizTable.svelte"
  import Container from "./shared/Container.svelte"
  import Modal from "./shared/Modal.svelte"
  import { getCurrentUserInfo, getDefaultHeaders } from "./utils.js"
  const dispatch = new createEventDispatcher()
  let quizTable
  let addQuizModal
  let editQuizModal
  let deleteQuizModal
  let selectedQuiz

  async function onRefreshQuizzes() {
    await quizTable.refreshQuizzes()
    await addQuizModal.close()
    await editQuizModal.close()
    await deleteQuizModal.close()
  }

  async function onEditQuiz(e) {
    selectedQuiz = e.detail
    await editQuizModal.toggle()
  }

  async function onDeleteQuiz(e) {
    selectedQuiz = e.detail
    await deleteQuizModal.toggle()
  }

  async function deleteQuiz() {
    await fetch(`/api/v1/quiz/${selectedQuiz.id}`, {
      headers: getDefaultHeaders(),
      method: "DELETE",
    }).then(async () => await onRefreshQuizzes())
  }
</script>

<button
  class="btn btn-primary"
  on:click={async () => await addQuizModal.toggle()}>New Quiz</button
>
<Modal bind:this={addQuizModal}>
  <AddQuiz slot="body" on:refreshQuizzes={onRefreshQuizzes} />
</Modal>
<br />
<QuizTable
  bind:this={quizTable}
  on:editQuiz={onEditQuiz}
  on:deleteQuiz={onDeleteQuiz}
  canSelect={false}
  canEdit={true}
  canDelete={true}
/>
<Modal bind:this={editQuizModal}>
  <EditQuiz
    slot="body"
    quiz={selectedQuiz}
    on:refreshQuizzes={onRefreshQuizzes}
  />
</Modal>
<Modal bind:this={deleteQuizModal}>
  <span slot="body" class="m-3">
    Are you Sure you want to delete quiz: "{selectedQuiz.title}" by {selectedQuiz
      .teacher.userInfo.user.name}?
  </span>
  <button
    slot="footer"
    class="btn btn-danger"
    on:click={async () => await deleteQuiz()}>Delete</button
  >
</Modal>
