<script>
  import { onMount, createEventDispatcher } from "svelte"
  import { getDefaultHeaders } from "../utils.js"
  const dispatch = new createEventDispatcher()

  let title
  let description

  async function newAnouncements() {
    await fetch("/api/v1/anouncement", {
      headers: getDefaultHeaders(),
      method: "POST",
      body: JSON.stringify({
        title,
        description,
      }),
    }).then(async () => await dispatch("refreshAnouncements"))
  }
</script>

<div class="mb-3 row">
  <label
    >Title: <input class="form-control" type="text" bind:value={title} /></label
  >
</div>

<div class="mb-3 row">
  <label
    >Description: <input
      class="form-control"
      type="text"
      bind:value={description}
    /></label
  >
</div>
<div class="modal-footer ">
  <button class="btn btn-success" on:click={async () => await newAnouncements()}
    >Submit</button
  >
</div>
