<script>
  import Contact from "./Contact.svelte"

  import About from "./About.svelte"

  import Home from "./Home.svelte"
  import Quizzes from "./Quizzes.svelte"
  import QuizResults from "./QuizResults.svelte"
  import TakeQuiz from "./TakeQuiz.svelte"
  import Students from "./Students.svelte"
  import Teachers from "./Teachers.svelte"
  import Sections from "./Sections.svelte"
  import Subjects from "./Subjects.svelte"
  import Anouncements from "./Anouncements.svelte"
  import LearningMaterials from "./LearningMaterials.svelte"
  import Courses from "./Courses.svelte"

  import Footer from "./shared/Footer.svelte"
  import Container from "./shared/Container.svelte"
  import NavBar from "./shared/NavBar.svelte"
  import { onMount } from "svelte"
  import Cookies from "js-cookie"
  import { getCurrentUserInfo, getDecodedToken } from "./utils.js"
  let userInfo
  let username = ""
  let password = ""
  let alertMessage = ""
  let isLoggedOn = false

  async function authenticate(uname, pword) {
    await fetch("/api/v1/userInfo/auth", {
      headers: new Headers({ "Content-Type": "application/json" }),
      method: "POST",
      body: JSON.stringify({
        username: uname,
        password: pword,
      }),
    })
      .then((r) => r.json())
      .then((d) => (isLoggedOn = d.status == "success"))
  }

  async function submitCredentials() {
    Cookies.remove("token")
    await authenticate(username, password)
    if (!isLoggedOn) {
      alertMessage = "Invalid Credentials"
    }

    links = [
      { class: "home", title: "Home" },
      { class: "contact", title: "Contact Us" },
      { class: "about", title: "About Us" },
      { class: "quizzes", title: "Quizzes" },
      { class: "quizResults", title: "Quiz results" },
      { class: "takeQuiz", title: "Take quiz" },
      { class: "courses", title: "Courses" },
      { class: "students", title: "Students" },
      { class: "teachers", title: "Teachers" },
      { class: "sections", title: "Sections" },
      { class: "subjects", title: "Subjects" },
      { class: "learningMaterials", title: "Learning materials" },
      { class: "courses", title: "Courses" },
    ]
    tabChange({ detail: "home" })
  }

  async function submitFromEnter(e) {
    if (e.key == "Enter") {
      return submitCredentials()
    }
  }

  function logOut() {
    Cookies.remove("token")
    isLoggedOn = false
  }

  async function checkCookie() {
    userInfo = getCurrentUserInfo()

    if (userInfo) {
      var decoded = getDecodedToken()
      var exp = decoded?.exp
      isLoggedOn = new Date(exp * 1000) >= new Date()
    } else {
      isLoggedOn = false
    }
  }

  onMount(async () => {
    await checkCookie()
    let filteredLink = links.filter(
      (i) => i.class == document.location.hash.substr(1)
    )[0]?.class
    tabChange({ detail: "home" })

    if (filteredLink != undefined) {
      activeLink = filteredLink
    }
  })

  let links = [
    { class: "home", title: "Home" },
    { class: "contact", title: "Contact Us" },
    { class: "about", title: "About Us" },
    { class: "quizzes", title: "Quizzes" },
    { class: "quizResults", title: "Quiz results" },
    { class: "takeQuiz", title: "Take quiz" },
    { class: "courses", title: "Courses" },
    { class: "students", title: "Students" },
    { class: "teachers", title: "Teachers" },
    { class: "sections", title: "Sections" },
    { class: "subjects", title: "Subjects" },
    { class: "anouncements", title: "Anouncements" },
    { class: "learningMaterials", title: "Learning materials" },
  ]
  const roleViews = [
    { view: "home", roles: ["Admin", "Teacher", "Student", ""] },
    { view: "contact", roles: ["Admin", "Teacher", "Student", ""] },
    { view: "about", roles: ["Admin", "Teacher", "Student", ""] },
    { view: "quizzes", roles: ["Teacher"] },
    { view: "quizResults", roles: ["Teacher"] },
    { view: "takeQuiz", roles: ["Student"] },
    { view: "courses", roles: ["Admin", "Teacher", "Student", ""] },
    { view: "students", roles: ["Admin"] },
    { view: "teachers", roles: ["Admin"] },
    { view: "sections", roles: ["Admin"] },
    { view: "subjects", roles: ["Admin"] },
    { view: "anouncements", roles: ["Teacher", "Student"] },
    { view: "learningMaterials", roles: ["Teacher"] },
  ]
  let activeLink

  const tabChange = async (e) => {
    activeLink = e.detail
    await checkCookie()

    links = links.filter(
      (l) =>
        roleViews.filter(
          (r) => r.view == l.class && r.roles.includes(userInfo?.user.role)
        ).length > 0
    )
  }

  const viewByRoles = () =>
    roleViews
      .filter((r) => r.view == activeLink)[0]
      ?.roles.includes(userInfo?.user.role)
</script>

{#if isLoggedOn}
  <div class="wrapper">
    <NavBar {links} on:tabChange={tabChange} on:logOut={logOut} />
    <div class="main">
      <nav class="navbar navbar-expand navbar-light navbar-bg">
        <a href={`#${activeLink}`} class="sidebar-toggle js-sidebar-toggle">
          <i class="hamburger align-self-center" />
        </a>
        <div class="navbar-collapse collapse">
          <ul class="navbar-nav navbar-align">
            <li class="nav-item dropdown">
              <a
                class="nav-link dropdown-toggle d-none d-sm-inline-block"
                href={`#${activeLink}`}
                data-bs-toggle="dropdown"
                ><span class="text-dark">{userInfo?.user.name}</span>
              </a>
              <div class="dropdown-menu dropdown-menu-end">
                <a
                  on:click={(e) => {
                    e.preventDefault()
                    logOut()
                  }}
                  class="dropdown-item"
                  href={`#${activeLink}`}>Log out</a
                >
              </div>
            </li>
          </ul>
        </div>
      </nav>
      <Container>
        {#if viewByRoles() && activeLink == "quizzes"}
          <Quizzes />
        {:else if viewByRoles() && activeLink == "quizResults"}
          <QuizResults />
        {:else if viewByRoles() && activeLink == "takeQuiz"}
          <TakeQuiz />
        {:else if viewByRoles() && activeLink == "students"}
          <Students />
        {:else if viewByRoles() && activeLink == "courses"}
          <Courses />
        {:else if viewByRoles() && activeLink == "teachers"}
          <Teachers />
        {:else if viewByRoles() && activeLink == "sections"}
          <Sections />
        {:else if viewByRoles() && activeLink == "subjects"}
          <Subjects />
        {:else if viewByRoles() && activeLink == "anouncements"}
          <Anouncements />
        {:else if viewByRoles() && activeLink == "learningMaterials"}
          <LearningMaterials />
        {:else if activeLink == "contact"}
          <Contact />
        {:else if activeLink == "about"}
          <About />
        {:else}
          <Home />
        {/if}
      </Container>
      <Footer />
    </div>
  </div>
{:else}
  <main class="d-flex w-100" id="loginMain">
    <div class="container d-flex flex-column">
      <div class="row vh-100">
        <div class="col-sm-10 col-md-8 col-lg-6 mx-auto d-table h-100">
          <div class="d-table-cell align-middle">
            <div class="card">
              <div class="card-body">
                <div class="m-sm-4">
                  <form>
                    <div class="mb-3">
                      <label for="username" class="form-label">Username</label>
                      <input
                        on:keypress={submitFromEnter}
                        bind:value={username}
                        id="username"
                        type="text"
                        class="form-control form-control-lg"
                        placeholder="Username"
                        required="required"
                      />
                    </div>
                    <div class="mb-3">
                      <label for="password" class="form-label">Password</label>
                      <input
                        on:keypress={submitFromEnter}
                        bind:value={password}
                        id="password"
                        type="password"
                        class="form-control form-control-lg"
                        placeholder="Password"
                        required="required"
                      />
                    </div>
                    <div class="text-center mt-3">
                      <button
                        on:click={async (e) => {
                          e.preventDefault()
                          await submitCredentials()
                        }}
                        class="btn btn-lg btn-primary">Sign in</button
                      >
                    </div>
                    {#if alertMessage != ""}
                      <div
                        class="text-center mt-3 alert alert-danger alert-dismissible"
                        role="alert"
                      >
                        <button
                          type="button"
                          class="btn-close"
                          data-bs-dismiss="alert"
                          aria-label="Close"
                        />
                        <div class="alert-message">
                          <strong>{alertMessage}</strong>
                          it out!
                        </div>
                      </div>
                    {/if}
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </main>
{/if}

<style>
  #loginMain {
    background-image: url("/images/1483563708.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }

  .alert-message {
    box-sizing: border-box;
    padding: 0.95rem;
    width: 100%;
  }

  .alert-dismissible .btn-close {
    padding: 1.1875rem 0.95rem;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 2;
  }

  .alert {
    display: flex;
    padding: 0;
  }

  .alert-danger {
    background-color: #f8d7da;
    border-color: #f5c2c7;
    color: #842029;
  }
  .alert-dismissible {
    padding-right: 2.85rem;
  }
  .alert {
    border: 0 solid transparent;
    border-radius: 0.2rem;
    margin-bottom: 1rem;
    padding: 0.95rem;
    position: relative;
  }
</style>
