<script>
  import Container from "./shared/Container.svelte"
  import QuizResultTable from "./QuizResult/QuizResultTable.svelte"
  import { createEventDispatcher, onMount } from "svelte"
  import EditQuizResult from "./QuizResult/EditQuizResult.svelte"
  import Modal from "./shared/Modal.svelte"
  import { getCurrentUserInfo, getDefaultHeaders } from "./utils.js"
  const dispatch = new createEventDispatcher()
  let quizResultTable
  let editQuizResultModal
  let deleteQuizResultModal
  let selectedQuizResult

  async function onRefreshQuizResults() {
    await quizResultTable.refreshQuizResults()
    await editQuizResultModal.close()
    await deleteQuizResultModal.close()
  }

  async function onEditQuizResult(e) {
    selectedQuizResult = e.detail
    await editQuizResultModal.toggle()
  }

  async function onDeleteQuizResult(e) {
    selectedQuizResult = e.detail
    await deleteQuizResultModal.toggle()
  }

  async function deleteQuizResult() {
    await fetch(`/api/v1/quizResult/${selectedQuizResult.id}`, {
      headers: getDefaultHeaders(),
      method: "DELETE",
    }).then(async () => await onRefreshQuizResults())
  }
</script>

<QuizResultTable
  bind:this={quizResultTable}
  on:editQuizResult={onEditQuizResult}
  on:deleteQuizResult={onDeleteQuizResult}
  canSelect={false}
  canEdit={true}
  canDelete={true}
/>
<Modal bind:this={editQuizResultModal}>
  <EditQuizResult
    slot="body"
    quizResult={selectedQuizResult}
    on:refreshQuizResults={onRefreshQuizResults}
  />
</Modal>
<Modal bind:this={deleteQuizResultModal}>
  <span slot="body" class="m-3">
    Are you Sure you want to delete quiz result: "{selectedQuizResult.quiz
      .title}" answered by "{selectedQuizResult.student.userInfo.user.name}"?
  </span>
  <button
    slot="footer"
    class="btn btn-danger"
    on:click={async () => await deleteQuizResult()}>Delete</button
  >
</Modal>
