<script>
  import { onMount, createEventDispatcher } from "svelte"
  import { getCurrentUserInfo, getDefaultHeaders } from "../utils.js"
  const dispatch = new createEventDispatcher()

  export let subject
  let name

  onMount(async () => {
    name = subject.name
  })

  async function updateSubjects() {
    await fetch("/api/v1/subject", {
      headers: getDefaultHeaders(),
      method: "PUT",
      body: JSON.stringify({
        id: subject.id,
        name,
      }),
    }).then(async () => await dispatch("refreshSubjects"))
  }
</script>

<div class="mb-3 row">
  <label>Name: <input type="text" bind:value={name} /></label>
</div>
<div class="modal-footer ">
  <button class="btn btn-warning" on:click={async () => await updateSubjects()}
    >Submit</button
  >
</div>
