<script>
  import { createEventDispatcher, onMount } from "svelte";
  import { getCurrentUserInfo, getDefaultHeaders } from "./utils.js";
  const dispatch = new createEventDispatcher();
  import Card from "./shared/Card.svelte";

  let subjects = [];

  onMount(async () => {
    subjects = await fetch("/api/v1/subject", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json());
  });
</script>

<div class="row">
  {#each subjects as subject}
    <div class="col-12 col-md-6 col-lg-4">
      <Card
        image={`https://via.placeholder.com/150x200.png?text=${subject.name}`}
        title={subject.name}
      >
        Description
      </Card>
    </div>
  {/each}
</div>
