<script>
  import { createEventDispatcher, onMount } from "svelte"
  import AddTeacher from "./Teacher/AddTeacher.svelte"
  import EditTeacher from "./Teacher/EditTeacher.svelte"
  import TeacherTable from "./Teacher/TeacherTable.svelte"
  import Container from "./shared/Container.svelte"
  import Modal from "./shared/Modal.svelte"
  import { getCurrentUserInfo, getDefaultHeaders } from "./utils.js"
  const dispatch = new createEventDispatcher()
  let teacherTable
  let addTeacherModal
  let editTeacherModal
  let deleteTeacherModal
  let selectedTeacher

  async function onRefreshTeachers() {
    await teacherTable.refreshTeachers()
    await addTeacherModal.close()
    await editTeacherModal.close()
    await deleteTeacherModal.close()
  }

  async function onEditTeacher(e) {
    selectedTeacher = e.detail
    await editTeacherModal.toggle()
  }

  async function onDeleteTeacher(e) {
    selectedTeacher = e.detail
    await deleteTeacherModal.toggle()
  }

  async function deleteTeacher() {
    await fetch(`/api/v1/teacher/${selectedTeacher.id}`, {
      headers: getDefaultHeaders(),
      method: "DELETE",
    }).then(async () => await onRefreshTeachers())
  }
</script>

<button
  class="btn btn-primary"
  on:click={async () => await addTeacherModal.toggle()}>New Teacher</button
>
<Modal bind:this={addTeacherModal}>
  <AddTeacher slot="body" on:refreshTeachers={onRefreshTeachers} />
</Modal>
<br />
<TeacherTable
  bind:this={teacherTable}
  on:editTeacher={onEditTeacher}
  on:deleteTeacher={onDeleteTeacher}
  canSelect={false}
  canEdit={true}
  canDelete={true}
/>
<Modal bind:this={editTeacherModal}>
  <EditTeacher
    slot="body"
    teacher={selectedTeacher}
    on:refreshTeachers={onRefreshTeachers}
  />
</Modal>
<Modal bind:this={deleteTeacherModal}>
  <span slot="body" class="m-3">
    Are you Sure you want to delete teacher: "{selectedTeacher.user.name}"?
  </span>
  <button
    slot="footer"
    class="btn btn-danger"
    on:click={async () => await deleteTeacher()}>Delete</button
  >
</Modal>
