<script>
  import { onMount, createEventDispatcher } from "svelte"
  import { getCurrentUserInfo, getDefaultHeaders } from "../utils.js"
  const dispatch = new createEventDispatcher()

  let username
  let password
  let teacherName
  let status
  let birthday
  let gender
  let address
  let contactNumber

  async function newTeachers() {
    await fetch("/api/v1/teacher", {
      headers: getDefaultHeaders(),
      method: "POST",
      body: JSON.stringify({
        userInfo: {
          user: {
            username,
            password,
            name: teacherName,
            status,
          },
          birthday: new Date(birthday).toISOString(),
          gender,
          address,
          contactNumber,
        },
      }),
    }).then(async () => await dispatch("refreshTeachers"))
  }
</script>

<div class="mb-3 row">
  <label
    >Username: <input
      class="form-control"
      type="text"
      bind:value={username}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Password: <input
      class="form-control"
      type="password"
      bind:value={password}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Name: <input
      class="form-control"
      type="text"
      bind:value={teacherName}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Status: <input
      class="form-control"
      type="text"
      bind:value={status}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Birthday: <input
      class="form-control"
      type="datetime-local"
      bind:value={birthday}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Gender: <input
      class="form-control"
      type="text"
      bind:value={gender}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Address: <input
      class="form-control"
      type="text"
      bind:value={address}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Contact Number: <input
      class="form-control"
      type="text"
      bind:value={contactNumber}
    /></label
  >
</div>
<div class="modal-footer ">
  <button class="btn btn-success" on:click={async () => await newTeachers()}
    >Submit</button
  >
</div>
