<script>
  import { onMount, createEventDispatcher } from "svelte"
  import {
    getCurrentUserInfo,
    formatDate,
    getDefaultHeaders,
  } from "../utils.js"
  const dispatch = new createEventDispatcher()
  export let canSelect = true
  export let canEdit
  export let canDelete
  let teachers = []

  onMount(async () => {
    await refreshTeachers()
  })

  export async function refreshTeachers() {
    teachers = await fetch("/api/v1/teacher", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json())
  }
</script>

{#if teachers.length > 0}
  <table class="table table-striped">
    <thead>
      <tr>
        <td>Username</td>
        <td>Name</td>
        <td>Status</td>
        <td>Birthday</td>
        <td>Gender</td>
        <td>Address</td>
        <td>Contact number</td>
        <td>Action</td>
      </tr>
    </thead>
    <tbody>
      {#each teachers as teacher}
        <tr>
          <td>{teacher.userInfo.user.username}</td>
          <td>{teacher.userInfo.user.name}</td>
          <td>{teacher.userInfo.user.status}</td>
          <td>{formatDate(teacher.userInfo.birthday)}</td>
          <td>{teacher.userInfo.gender}</td>
          <td>{teacher.userInfo.address}</td>
          <td>{teacher.userInfo.contactNumber}</td>
          <td>
            {#if canSelect}
              <button
                on:click={async () => await dispatch("selectTeacher", teacher)}
                >Select</button
              >
            {/if}
            {#if canEdit}
              <button
                style="padding: 0; border: none; background: none;"
                on:click={async () => await dispatch("editTeacher", teacher)}
                ><svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  class="feather feather-edit-2 align-middle"
                  ><path
                    d="M17 3a2.828 2.828 0 1 1 4 4L7.5 20.5 2 22l1.5-5.5L17 3z"
                  /></svg
                ></button
              >
            {/if}
            {#if canDelete}
              <button
                style="padding: 0; border: none; background: none;"
                on:click={async () => await dispatch("deleteTeacher", teacher)}
                ><svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  class="feather feather-trash align-middle"
                  ><polyline points="3 6 5 6 21 6" /><path
                    d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"
                  /></svg
                ></button
              >
            {/if}
          </td>
        </tr>
      {/each}
    </tbody>
  </table>
{/if}
