<h1>Vision and Mission</h1>
<div class="row">
  <h3>Our School Vision</h3>
  <p class="text-justify">
    Our St Therese’s school vision is: “Inspired by Christ We journey together
    Embracing, faith, life and learning”   oSt Therese’s School, with Christ as
    our role model and in union with the Church’s mission shall provide
    on-going, holistic education for our students. oWe respect the uniqueness of
    all individuals in the diverse and changing culture of our school community.
    oSt Therese’s values the importance of the family in the partnership of
    education. oOur school shall foster a quality learning environment as a
    community which includes children, parents, staff, Parish Priest,
    parishioners and Catholic Education Services.
  </p>
</div>
<div class="row">
  <h3>Our School Motto Our</h3>
  <p class="text-justify">
    St Therese’s school motto is: Multis Conjunctis, Magna Perfecta – “Through
    the work of many, united as one, great things are done” Our school
    guidelines, our approach to pastoral care and the way in which we relate to
    each other as members of a learning community reflect the values taught to
    us by Jesus and modelled by our patroness, St Therese, The Little Flower of
    Jesus.
  </p>
</div>
<div class="row">
  <h3>Our School Mission</h3>
  <p class="text-justify">
    As the community of St Therese’s Catholic School, we are called to offer
    quality Catholic education so that each person may know and come to identify
    more fully with the living Spirit of Christ: oby being welcoming, respecting
    the uniqueness of all, made in the image and likeness of God; oby building
    relationships that strengthen a respectful, safe and supportive learning
    community; oby living, celebrating and enriching our Catholic Christian
    tradition through worship, evangelisation, spiritual and faith formation;
    oby being worthy stewards of God’s Creation; oby ensuring that learning is
    personalised, inclusive and holistic, in partnership with all.
  </p>
</div>
