<script>
  import { onMount, createEventDispatcher } from "svelte"
  import { getCurrentUserInfo, getDefaultHeaders } from "../utils.js"
  const dispatch = new createEventDispatcher()

  let sections = []
  let username
  let password
  let studentName
  let status
  let birthday
  let gender
  let address
  let contactNumber
  let section
  let guardianName
  let gurdianContactNumber

  onMount(async () => {
    sections = await fetch("/api/v1/section", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json())
  })

  async function newStudents() {
    await fetch("/api/v1/student", {
      headers: getDefaultHeaders(),
      method: "POST",
      body: JSON.stringify({
        userInfo: {
          user: {
            username,
            password,
            name: studentName,
            status,
          },
          birthday: new Date(birthday).toISOString(),
          gender,
          address,
          contactNumber,
        },
        section: {
          id: section.id,
        },
        guardian: {
          name: guardianName,
          contactNumber: gurdianContactNumber,
        },
      }),
    }).then(async () => await dispatch("refreshStudents"))
  }
</script>

<div class="mb-3 row">
  <label
    >Username: <input
      class="form-control"
      type="text"
      bind:value={username}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Password: <input
      class="form-control"
      type="password"
      bind:value={password}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Name: <input
      class="form-control"
      type="text"
      bind:value={studentName}
    /></label
  >
</div>
<div class="mb-3 row">
  <label>
    Section:
    <select class="form-control" bind:value={section}>
      <option value="">Select a Section</option>
      {#each sections as section}
        <option value={section}>{section.name}</option>
      {/each}
    </select>
  </label>
</div>
<div class="mb-3 row">
  <label
    >Status: <input
      class="form-control"
      type="text"
      bind:value={status}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Birthday: <input
      class="form-control"
      type="datetime-local"
      bind:value={birthday}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Gender: <input
      class="form-control"
      type="text"
      bind:value={gender}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Address: <input
      class="form-control"
      type="text"
      bind:value={address}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Contact Number: <input
      class="form-control"
      type="text"
      bind:value={contactNumber}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Guardian Name: <input
      class="form-control"
      type="text"
      bind:value={guardianName}
    /></label
  >
</div>
<div class="mb-3 row">
  <label
    >Guardian Contact number: <input
      class="form-control"
      type="text"
      bind:value={gurdianContactNumber}
    /></label
  >
</div>
<div class="modal-footer ">
  <button class="btn btn-success" on:click={async () => await newStudents()}
    >Submit</button
  >
</div>
