<script>
  import { onMount, createEventDispatcher } from "svelte"
  import Cookies from "js-cookie"
  import { getCurrentUserInfo, getDefaultHeaders } from "../utils.js"
  const dispatch = new createEventDispatcher()
  export let canSelect = true
  export let canEdit
  export let canDelete
  let learningMaterials = []
  const currentUserInfo = getCurrentUserInfo()

  onMount(async () => {
    await refreshLearningMaterials()
  })

  export async function refreshLearningMaterials() {
    learningMaterials = await fetch("/api/v1/learningMaterial/currentUser", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json())
  }

  async function downloadFile(learningMaterial) {
    await fetch(
      `/api/v1/learningMaterial/${learningMaterial.id}/downloadFile`,
      {
        headers: {
          Authorization: `Bearer ${Cookies.get("token")}`,
        },
      }
    ).then(async (r) => {
      if (r.status === 200) {
        let b = await r.blob()
        let objectUrl = window.URL.createObjectURL(b)
        let anchor = document.createElement("a")
        anchor.href = objectUrl
        anchor.download = learningMaterial.filename
        anchor.click()
        window.URL.revokeObjectURL(objectUrl)
      }
    })
  }
</script>

{#if learningMaterials.length > 0}
  <table class="table table-striped">
    <thead>
      <tr>
        <th>Title</th>
        {#if currentUserInfo.user?.role == "Admin"}
          <th>Teacher</th>
        {/if}
        <th>Subject</th>
        <th>Filename</th>
        <th>Description</th>
        <th>Actions</th>
      </tr>
    </thead>
    <tbody>
      {#each learningMaterials as learningMaterial}
        <tr>
          <td>{learningMaterial.title}</td>
          {#if currentUserInfo.user?.role == "Admin"}
            <td>{learningMaterial.teacher.userInfo.user.name}</td>
          {/if}
          <td>{learningMaterial.subject.name}</td>
          <td>
            <button on:click={async () => await downloadFile(learningMaterial)}>
              Download {learningMaterial.filename}
            </button>
          </td>
          <td>{learningMaterial.description}</td>
          <td>
            {#if canSelect}
              <button
                on:click={async () =>
                  await dispatch("selectLearningMaterial", learningMaterial)}
                >Select</button
              >
            {/if}
            {#if canEdit}
              <button
                style="padding: 0; border: none; background: none;"
                on:click={async () =>
                  await dispatch("editLearningMaterial", learningMaterial)}
                ><svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  class="feather feather-edit-2 align-middle"
                  ><path
                    d="M17 3a2.828 2.828 0 1 1 4 4L7.5 20.5 2 22l1.5-5.5L17 3z"
                  /></svg
                ></button
              >
            {/if}
            {#if canDelete}
              <button
                style="padding: 0; border: none; background: none;"
                on:click={async () =>
                  await dispatch("deleteLearningMaterial", learningMaterial)}
                ><svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  class="feather feather-trash align-middle"
                  ><polyline points="3 6 5 6 21 6" /><path
                    d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"
                  /></svg
                ></button
              >
            {/if}
          </td>
        </tr>
      {/each}
    </tbody>
  </table>
{/if}
