<script>
  import { createEventDispatcher, onMount } from "svelte"
  import AddAnouncement from "./Anouncement/AddAnouncement.svelte"
  import EditAnouncement from "./Anouncement/EditAnouncement.svelte"
  import AnouncementTable from "./Anouncement/AnouncementTable.svelte"
  import Container from "./shared/Container.svelte"
  import Modal from "./shared/Modal.svelte"
  import { getCurrentUserInfo, getDefaultHeaders } from "./utils.js"
  import AnouncementsPage from "./Anouncement/AnouncementsPage.svelte"
  const dispatch = new createEventDispatcher()
  let anouncementTable
  let addAnouncementModal
  let editAnouncementModal
  let deleteAnouncementModal
  let selectedAnouncement
  let anouncementsPage

  async function onRefreshAnouncements() {
    await anouncementTable.refreshAnouncements()
    await anouncementsPage.refreshAnouncements()
    await addAnouncementModal.close()
    await editAnouncementModal.close()
    await deleteAnouncementModal.close()
  }

  async function onEditAnouncement(e) {
    selectedAnouncement = e.detail
    await editAnouncementModal.toggle()
  }

  async function onDeleteAnouncement(e) {
    selectedAnouncement = e.detail
    await deleteAnouncementModal.toggle()
  }

  async function deleteAnouncement() {
    await fetch(`/api/v1/anouncement/${selectedAnouncement.id}`, {
      headers: getDefaultHeaders(),
      method: "DELETE",
    }).then(async () => await onRefreshAnouncements())
  }

  const months = {
    0: "January",
    1: "February",
    2: "March",
    3: "April",
    4: "May",
    5: "June",
    6: "July",
    7: "August",
    8: "September",
    9: "October",
    10: "November",
    11: "December",
  }

  const formatDate = (d) => {
    d = new Date(d)
    return `${
      months[d.getMonth()]
    } ${d.getDate()}, ${d.getFullYear()} ${d.toLocaleTimeString()}`
  }
</script>

<button
  class="btn btn-primary"
  on:click={async () => await addAnouncementModal.toggle()}
  >New Anouncement</button
>
<Modal bind:this={addAnouncementModal}>
  <AddAnouncement slot="body" on:refreshAnouncements={onRefreshAnouncements} />
</Modal>
<br />
<AnouncementTable
  bind:this={anouncementTable}
  on:editAnouncement={onEditAnouncement}
  on:deleteAnouncement={onDeleteAnouncement}
  canSelect={false}
  canEdit={true}
  canDelete={true}
/>
<AnouncementsPage bind:this={anouncementsPage} />
<Modal bind:this={editAnouncementModal}>
  <EditAnouncement
    slot="body"
    anouncement={selectedAnouncement}
    on:refreshAnouncements={onRefreshAnouncements}
  />
</Modal>
<Modal bind:this={deleteAnouncementModal}>
  <span slot="body" class="m-3">
    Are you Sure you want to delete anouncement: "{selectedAnouncement.title}"
    created at "{formatDate(selectedAnouncement.dateCreated)}"?
  </span>
  <button
    slot="footer"
    class="btn btn-danger"
    on:click={async () => await deleteAnouncement()}>Delete</button
  >
</Modal>
