<h1>Welcome to St Therese’s School</h1>
<div class="row">
  <p class="text-justify">
    Welcome to St Therese’s School, Bentley Park, where our students are at the
    centre of everything we do. We value parents as the first teachers of their
    children and work with them to mirror the values that are important to
    families, because we, like parents, want children to be the best version of
    themselves. Improvement and transformation at St Therese’s designed to drive
    our focus on wellbeing and learning for all students are being inspired by
    the principles of Reggio Emilia. Our moral imperative is simple; we want to
    raise the bar on student wellbeing and learning for ALL students so that
    they can achieve and be the best version of themselves. Like all schools, we
    offer the Australian curriculum that engages in English, Maths, Science and
    Technology. As a Catholic school, we offer a holistic development of the
    whole child – intellectually, spiritually, socially, emotionally and
    physically. Students are encouraged to value diversity, develop a sense of
    social justice and become valued, contributing members of society. At St
    Therese’s, we combine our proud history and strong foundations with a
    contemporary thirst for lifelong learning and continual improvement. We have
    an emphasis on literacy and numeracy and a focus on the arts; particularly
    visual art and dance. To engage our students fully in the curriculum we
    provide an emersion program where our students engage in learning from Prep
    to Year 6. And in Year 6 they have an opportunity to travel to our sister
    school in Taiwan to go to school there for a week and live with a school
    family. As a parent considering whether to enrol your child at St Therese’s,
    you may have many questions around Catholic school enrolment, like “Does my
    child have to be Catholic?” “Is it expensive?” “What if I can’t afford it?”
    “Is there a catchment area?” Please visit Cairns Catholic Education’s
    website and read the Mythbusting FAQs for the answer to these and other
    questions. Enrolment enquiries are always welcome at St Therese’s and
    prospective parents are encouraged to request a personalised tour of the
    school and a chat with our principal to answer any questions you may have.
    We look forward to enthusiastically welcoming you to our school community.
  </p>
</div>
