<script>
  import { createEventDispatcher, onMount } from "svelte"
  import AddStudent from "./Student/AddStudent.svelte"
  import EditStudent from "./Student/EditStudent.svelte"
  import StudentTable from "./Student/StudentTable.svelte"
  import Container from "./shared/Container.svelte"
  import Modal from "./shared/Modal.svelte"
  import { getCurrentUserInfo, getDefaultHeaders } from "./utils.js"
  const dispatch = new createEventDispatcher()
  let studentTable
  let addStudentModal
  let editStudentModal
  let deleteStudentModal
  let selectedStudent

  async function onRefreshStudents() {
    await studentTable.refreshStudents()
    await addStudentModal.close()
    await editStudentModal.close()
    await deleteStudentModal.close()
  }

  async function onEditStudent(e) {
    selectedStudent = e.detail
    await editStudentModal.toggle()
  }

  async function onDeleteStudent(e) {
    selectedStudent = e.detail
    await deleteStudentModal.toggle()
  }

  async function deleteStudent() {
    await fetch(`/api/v1/student/${selectedStudent.id}`, {
      headers: getDefaultHeaders(),
      method: "DELETE",
    }).then(async () => await onRefreshStudents())
  }
</script>

<button
  on:click={async () => await addStudentModal.toggle()}
  type="button"
  class="btn btn-primary">New Student</button
>
<Modal bind:this={addStudentModal}>
  <AddStudent slot="body" on:refreshStudents={onRefreshStudents} />
</Modal>
<br />
<StudentTable
  bind:this={studentTable}
  on:editStudent={onEditStudent}
  on:deleteStudent={onDeleteStudent}
  canSelect={false}
  canEdit={true}
  canDelete={true}
/>
<Modal bind:this={editStudentModal}>
  <EditStudent
    slot="body"
    student={selectedStudent}
    on:refreshStudents={onRefreshStudents}
  />
</Modal>
<Modal bind:this={deleteStudentModal}>
  <span slot="body" class="m-3">
    Are you Sure you want to delete student: "{selectedStudent.userInfo.user
      .name}" of section "{selectedStudent.section.name}"?
  </span>
  <button
    slot="footer"
    class="btn btn-danger"
    on:click={async () => await deleteStudent()}>Delete</button
  >
</Modal>
