<h2>Contact Us</h2>
<!-- <form
  method="post"
  action="/contact#contact_form"
  id="contact_form"
  accept-charset="UTF-8"
  class="contact-form"
> -->
<input type="hidden" name="form_type" value="contact" /><input
  type="hidden"
  name="utf8"
  value="✓"
/>

<div class="mb-3 row">
  <label>Name: <input class="form-control" type="text" /></label>
</div>

<div class="mb-3 row">
  <label
    >Email: <input
      class="form-control"
      type="email"
      spellcheck="false"
      autocomplete="off"
      autocapitalize="off"
    /></label
  >
</div>

<div class="mb-3 row">
  <label
    >Contact number: <input
      class="form-control"
      type="tel"
      pattern="[0-9\-]*"
    /></label
  >
</div>

<div class="mb-3 row">
  <label>Message: <textarea class="form-control" /></label>
</div>

<button type="submit" class="btn btn-primary" value="Send">Send</button>
<!-- </form> -->
